:root {
  // THEME 1
  --primary-color: tomato;  
  --primary-color-hover: rgb(255, 87, 58);

  // THEME 2
  // --primary-color: rgb(0, 238, 255);  
  // --primary-color-hover: rgb(0, 200, 150);

  --bg-color: #222;
  --bg-dark-color: #191919;

  --transition: all .3s ease
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1rem;
  font-family: "Open Sans", sans-serif;
}

.formContainer {
  user-select: none;
  background-color: #151515;
  background-image: url(../src/img/pulseBg.svg); 
  background-size: 2.3rem;
  background-position: center;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  .material-symbols-outlined {
    font-variation-settings:
    'FILL' 0,
    'wght' 400,
    'GRAD' 0,
    'opsz' 50
  }

  .formWrapper {
    display: flex;
    flex-direction: column;

    
    // background-color:#fff;
    padding: 2rem 6rem;
    border-radius: 25px;
    gap: 1rem;

    background: var(--bg-dark-color);
    // border: 2px solid var(--primary-color);


    // BG PATTERN #####################################
    // background-image: url(../src/img/hand-bg.svg);
    // background-repeat: no-repeat;
    // background-size: 1rem;
    // background-position: center;
    // ################################################

    box-shadow: 3px 7px 11px rgba(0, 0, 0, .7);

    // IF LOGIN OR REGISTRATION IS UNAVAILABLE
    // &:active {
    //   box-shadow: inset 0px 0px 11px rgba(255, 99, 71, .2);
    // }

    // box-shadow:  5px 5px 11px #1a1a1a,
    //          -5px -5px 11px #2a2a2a85;

    .logo {
      color: var(--primary-color);
      font-size: 2.4rem;
      font-weight: bold;
      text-align: center;
    }

    .title {
      color: var(--primary-color);
      font-size: 1.2rem;
      text-align: center;
    }

    form {
      display: flex;
      flex-direction: column;
      gap: 1.5rem;

      input {
        padding: 1.5rem;
        border: none;
        border-bottom: 1px solid var(--primary-color);
        width: 22rem;
        outline: var(--primary-color);
        background: none;
        color: #fff;
        // color: var(--primary-color);
      &::placeholder {
        color: rgb(175,175,175);
      }
      }

      button {
        background-color: var(--primary-color);
        color: #fff;
        padding: 1rem;
        font-weight: bold;
        border: none;
        outline: none;
        cursor: pointer;
        border-radius: 5px;
        transition: var(--transition);
        letter-spacing: .1rem;
      
      &:hover {
        background-color: var(--primary-color-hover);
      }
      }

      label {
        display: flex;
        align-items: center;
        gap: 1rem;
        color: var(--primary-color);
        font-size: 1.2rem;
        cursor: pointer;
        width: max-content;
        padding: .3rem .7rem .3rem .6rem;
        border-radius: 5px;
        transition: var(--transition);
      &:hover {
        background-color: rgba(0,0,0,.1);
      }
        img {
          width: 3rem;
          height: 3rem;
        }
      }
    }
    p {
      text-align: center;
      color: var(--primary-color);
      font-size: 1.2rem;
      margin-top: 1rem;
    }

    .link {
      text-decoration: none;
      color: rgb(100,100,100);

      &:hover {
        color: rgb(200,200,200);
      }
    }
  }
}


.home {
  // user-select: none;
  // background: var(--bg-color);
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;

  background-color: #151515;
  background-image: url(../src/img/pulseBg.svg); 
  background-size: 2.3rem;
  background-position: center;

  .container {
    border-radius: 15px;
    width: 80%;
    // width: 65%;
    height: 80%;
    // height: 80%;
    display: flex;
    overflow: hidden;
    box-shadow: 3px 7px 11px rgba(0, 0, 0, .7);
    // border: 2px solid var(--primary-color);

    // box-shadow:  5px 5px 11px #1a1a1a,
    //          -5px -5px 11px #2a2a2aad;

    .sidebar {
      flex: 1;
      background-color: var(--bg-dark-color);
      border-right: 1px solid #121212;
      user-select: none;
      

      .navbar {
        display: flex;
        align-items: center;
        justify-content: space-between;
        background-color: var(--bg-dark-color);
        height: 3rem;
        padding: 2.5rem 2rem;
        color: #fff;

        .logoBox {
          display: flex;
          align-items: center;
          gap: .3rem;
        }
          .logo {
            font-size: 2rem;
            font-weight: bold;
            color: var(--primary-color);
          }
          img {
            height: 3rem;
            // width: 3rem;
            object-fit: cover;
          }

        .user {
          display: flex;
          align-items: center;
          gap: .7rem;
          font-size: 1rem;

          // span {
            
          // }

          img {
            background-color: grey;
            height: 2.4rem;
            width: 2.4rem;
            border-radius: 50%;
            object-fit: cover;
          }

          button {
            // background-color: var(--primary-color);
            background-color: #202020;
            line-height: 100%;
            color: #fff;
            border: none;
            font-size: 1rem;
            padding: .5rem .7rem;
            cursor: pointer;
            margin-left: 1rem;
            transition: var(--transition);
            border-radius: .2rem;

          &:hover {
            background-color: var(--primary-color-hover);
          }
          }
        }
      }

      .search {
        border-bottom: 1px solid var(--primary-color);
        // border-bottom: 1px solid #202020;

        .searchForm {
          padding: 1rem 2rem;

          input {
            background-color: transparent;
            border: none;
            outline: none;
            color: #fff;
          
          &::placeholder {
            color: rgb(100,100,100);
          }
          }
        }
      }
      // .chats {
      //   // overflow-y:scroll;
      // }
      .userChat {
        display: flex;
        align-items: center;
        gap: 1rem;
        padding: 1rem 2rem;
        color: #fff;
        cursor: pointer;

        transition: var(--transition);
      &:hover {
        background-color: var(--primary-color-hover);
        // background-color: #202020;
      }

        img {
          width: 4rem;
          height: 4rem;
          border-radius: 50%;
          object-fit: cover;
        }

        .userChatInfo {

          span {
            font-size: 1.4rem;
            font-weight: 500;
          }

          p {
            font-size: 1.2rem;
            color: lightgray;
          }
        }
      }
    }

    .chat {
      flex: 2;
      
      .chatInfo {
        height: 5rem;
        // background-color: var(--bg-dark-color);
        background-color: #202020;
        // background-color: teal;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 2.5rem 2rem;
        color: rgba(100,100,100);
        font-size: 1.3rem;
        font-weight: 600;

        // TODO: ADD SEPARATOR FOR BETWEEN CHAT AND SIDEBAR #border: bottom, left
        // border-left: 1px solid #111;

        .chatIcons {
          display: flex;
          gap: .5rem;

          img {
            height: 2.4rem;
            padding: .1rem .5rem .1rem .5rem;
            border-radius: .2rem;
            cursor: pointer;
            transition: var(--transition);
            filter: invert(1);

            &:hover {
              // background-color: rgba(0,0,0,.1);
              // background-color: var(--primary-color-hover);
              background-color: #E6E6E6;
            }
          }
        }
      }

      .messages {
        background-color: var(--bg-dark-color);
        padding: 2.5rem 2rem;
        height: calc(100% - 10rem);
        overflow-y: scroll;

        &::-webkit-scrollbar {
          width: .5rem;
        }
        &:hover::-webkit-scrollbar {
          // background: var(--bg-dark-color);
          background: transparent;
        }
        &::-webkit-scrollbar-thumb {
          // background: #303030;
          background: transparent;
          border-radius: 3px;
          // background: var(--primary-color-hover);
        }
        &:hover::-webkit-scrollbar-thumb {
          background: #303030;
        }

        .message {
          display: flex;
          gap: 2rem;
          margin-bottom: 2rem;

          .messageInfo {
            display: flex;
            flex-direction: column;
            color: rgba(100,100,100);
            font-weight: 200;
            font-size: 1rem;
            align-items: center;

            img {
              width: 4rem;
              height: 4rem;
              border-radius: 50%;
              object-fit: cover;
            }
          }

          .messageContent {
            max-width: 50%;
            display: flex;
            flex-direction: column;
            gap: 1rem;

            p {
              background-color: #202020;
              padding: 1rem 2rem;
              border-radius: 0 7px 7px 7px;
              color: #fff;
              letter-spacing: 1px;
              max-width: max-content;
            }
            img {
              width: 50%;
            }
          }

          &.owner {
            flex-direction: row-reverse;

            .messageContent {
              align-items: flex-end;

              p {
                background-color: var(--primary-color);
                border-radius: 7px 0px 7px 7px;
                color: #fff;
                letter-spacing: 1px;
              }
            }
          }
        }
      }

      .input {
        height: calc(5rem + 1px);
        height: 5rem;
        // background-color: var(--bg-dark-color);
        background-color: #202020;
        // background-color: teal;
        padding: 2.5rem 0 2.5rem 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        // border-top: 1px solid #121212;

        img {
          height: 5rem;
          padding: 1.1rem 1.1rem 1.1rem 1.1rem;
          border-radius: 0rem;
          border-bottom-left-radius: 0;
          border-bottom-right-radius: 0;
          cursor: pointer;
          transition: var(--transition);
          filter: invert(1);

          &:hover {
            // background-color: rgba(0,0,0,.1);
            // background-color: var(--primary-color-hover);
            background-color: #E6E6E6;
          }
        }

        input {
          background: none;
          border: none;
          outline: none;

          width: 100%;
          height: 5rem;
          color: #fff;
          font-size: 1.5rem;
          padding-left: .5rem;

          &::placeholder {
            color: rgb(100,100,100);
          }
        }

        .send {
          display: flex;
          align-items: center;
          // gap: .5rem;
          
          label {
            height: 5rem;
          }

          button {
            width: 7rem;
            height: 5rem;
            background: none;
            border: none;
            cursor: pointer;
            background-color: #202020;
            color: #fff;
            font-weight: 200;
            transition: var(--transition);

            &:hover {
              background-color: var(--primary-color-hover);
              // background-color: var(--bg-dark-color);
            }
          }
        }
      }
    }
  }
}


